const formatDate = (time) => {
    // console.log(time)
    let [year, month, date] = time.split("-")
    let newDate = date.substring(0, 2)
    let newYear = year.substring(2, 4)
    return month+'/'+newDate+'/'+newYear
}

const formatTime = (time) => {
    let offset = (new Date().getTimezoneOffset())/60
    let timeStr = time.split("T")
    let hours = timeStr[1].substring(0,2) - offset
    let mins = timeStr[1].substring(3,5)
    let ampm = ''
    if(hours>12) {
        hours-=12
        ampm="PM"
    } else if (hours===0) {
        hours=12
        ampm="AM"
    } if(hours<0) {
        hours+=12
        ampm="PM"
    } else {
        ampm="AM"
    }
    return hours + ':' + mins + ampm
}

function ContextAwareToggle({ children, eventKey }) {
    const currentEventKey = useContext(AccordionContext);
    
    const isCurrentEventKey = currentEventKey === eventKey;

    return (
        <Accordion.Toggle style={{'borderBottomRightRadius': isCurrentEventKey ? '0' : '', 'borderBottomLeftRadius': isCurrentEventKey ? '0' : '' }} block className='text-dark' as={Button}  variant='primary' eventKey={eventKey}>
            {children}
        </Accordion.Toggle>
    );
}

